.van-action-sheet {
  max-height: 90% !important;
  max-height: var(--action-sheet-max-height, 90%) !important;
  color: #323233;
  color: var(--action-sheet-item-text-color, #323233);
}
.van-action-sheet__item,
.van-action-sheet__cancel {
  height: auto;
  border: none;
  padding: 0.59733rem 0.68267rem;
  text-align: center;
  font-size: 0.68267rem;
  font-size: var(--action-sheet-item-font-size, 0.68267rem);
  line-height: 0.93867rem;
  line-height: var(--action-sheet-item-line-height, 0.93867rem);
  background-color: #fff;
  background-color: var(--action-sheet-item-background, #fff);
}
.van-action-sheet__item--hover,
.van-action-sheet__cancel--hover {
  background-color: #f2f3f5;
  background-color: var(--active-color, #f2f3f5);
}
.van-action-sheet__item::after,
.van-action-sheet__cancel::after {
  border-width: 0;
}
.van-action-sheet__cancel {
  color: #646566;
  color: var(--action-sheet-cancel-text-color, #646566);
}
.van-action-sheet__gap {
  display: block;
  height: 0.34133rem;
  height: var(--action-sheet-cancel-padding-top, 0.34133rem);
  background-color: #f7f8fa;
  background-color: var(--action-sheet-cancel-padding-color, #f7f8fa);
}
.van-action-sheet__item--disabled {
  color: #c8c9cc;
  color: var(--action-sheet-item-disabled-text-color, #c8c9cc);
}
.van-action-sheet__item--disabled.van-action-sheet__item--hover {
  background-color: #fff;
  background-color: var(--action-sheet-item-background, #fff);
}
.van-action-sheet__subname {
  margin-top: 0.34133rem;
  margin-top: var(--padding-xs, 0.34133rem);
  font-size: 0.512rem;
  font-size: var(--action-sheet-subname-font-size, 0.512rem);
  color: #969799;
  color: var(--action-sheet-subname-color, #969799);
  line-height: 0.85333rem;
  line-height: var(--action-sheet-subname-line-height, 0.85333rem);
}
.van-action-sheet__header {
  text-align: center;
  font-weight: 500;
  font-weight: var(--font-weight-bold, 500);
  font-size: 0.68267rem;
  font-size: var(--action-sheet-header-font-size, 0.68267rem);
  line-height: 2.048rem;
  line-height: var(--action-sheet-header-height, 2.048rem);
}
.van-action-sheet__description {
  text-align: center;
  padding: 0.85333rem 0.68267rem;
  padding: var(--padding, 0.85333rem) var(--padding-md, 0.68267rem);
  color: #969799;
  color: var(--action-sheet-description-color, #969799);
  font-size: 0.59733rem;
  font-size: var(--action-sheet-description-font-size, 0.59733rem);
  line-height: 0.85333rem;
  line-height: var(--action-sheet-description-line-height, 0.85333rem);
}
.van-action-sheet__close {
  position: absolute !important;
  top: 0;
  right: 0;
  line-height: inherit !important;
  padding: 0 0.68267rem;
  padding: var(--action-sheet-close-icon-padding, 0 0.68267rem);
  font-size: 0.93867rem !important;
  font-size: var(--action-sheet-close-icon-size, 0.93867rem) !important;
  color: #c8c9cc;
  color: var(--action-sheet-close-icon-color, #c8c9cc);
}
.van-action-sheet__loading {
  display: -webkit-flex !important;
  display: flex !important;
}
